#signUp{
    margin: 20px auto;
    height: 820px;
    width: 200px;
    border: 2px solid gray;
    padding: 30px 100px; 
    background-color: rgb(70, 140, 206);
    border-radius: 0 0 50px 50px;
}

#signUpSpan{
    margin-left: 50px;
    font-size: 40px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: darkred;
   
}

.input{
    height: 80px;
    width: 350px;
}

.Field{
    height: 50px;
    width: 350px;

}

.inputField{
    height: 25px;
    width: 200px;
    outline: none;
    border: 1px solid rgb(70, 140, 206);
    border-bottom: 3px solid rgb(12, 25, 85);
    padding-left: 10px;
    background-color: rgb(70, 140, 206);
}

.inputField:focus{
    outline: none;
    border: 1px solid rgb(70, 140, 206);
    background-color: rgb(70, 140, 206);;
}

.inline{
    display: inline-block;
    margin-right: 5px;
    margin-top: 8px;
}

.btn{
    height: 40px;
    width: 80px;
    border-radius: 15px;
    margin-top: 10px;
    margin-left: 60px;
}